/*============-============-============-============
                         VARIABLES
==============-============-============-==========*/

:root {
  --color-primary: #2980b9;
  --color-2: #16a085;
  --color-3: #8e44ad;
  --color-4: #34495e;
  --color-5: #27ae60;
  --color-white: #fdfdfd;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fdfdfd;
  background-color: var(--color-white);
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

img {
  width: 100%;
}

input {
  border: 1px solid #2980b9;
  border: 1px solid var(--color-primary);
}

.btn {
  outline: none;
  border-radius: 5px;
  cursor: pointer;
  padding: 5px 15px;
  font-size: 1.2rem;
  background-color: #fdfdfd;
  background-color: var(--color-white);
  color: #2980b9;
  color: var(--color-primary);
  border: 1px solid #2980b9;
  border: 1px solid var(--color-primary);
  -webkit-transition: all .3s ease-out;
  transition: all .3s ease-out;
}

.btn:hover {
  background-color: #2980b9;
  background-color: var(--color-primary);
  color: #fdfdfd;
  color: var(--color-white);
}

.container {
  padding: 1rem;
}

.photo-list {
  max-width: 1430px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  grid-template-rows: 200px;
  grid-auto-rows: minmax(350px, 1fr);
  grid-gap: 10px;
}

.photo-container {
  border-radius: 10px;
  overflow: hidden;
}

.photo  {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center center;
  -webkit-transition: -webkit-transform .5s cubic-bezier(.7,-0.39,.31,1.38);
  transition: -webkit-transform .5s cubic-bezier(.7,-0.39,.31,1.38);
  transition: transform .5s cubic-bezier(.7,-0.39,.31,1.38);
  transition: transform .5s cubic-bezier(.7,-0.39,.31,1.38), -webkit-transform .5s cubic-bezier(.7,-0.39,.31,1.38);
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
}

.photo:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.photo-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.photo-header span {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 2rem;
}

.photo-header form {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}

.photo-header .search-input {
  max-width: 200px;
  font-size: 1.2rem;
  padding: 5px 10px;
  border-radius: 5px;
  margin-right: .35rem;
  outline: none;
}

.photo-header .btn-container {
  display: flex;
}

.title {
  text-transform: capitalize;
}

.tag {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #2980b9;
  background-color: var(--color-primary);
  width: 100%;
  height: 100%;
  border-radius: 10px;
  -webkit-transition: background-color .3s ease-out;
  transition: background-color .3s ease-out;
}

.tag p {
  padding: 2rem;
  font-size: 1.8rem;
  font-weight: bold;
  color: #fdfdfd;
  color: var(--color-white);
  text-transform: capitalize;
  cursor: pointer;
  -webkit-transition: font-size .4s ease;
  transition: font-size .4s ease;
}

.tag p:hover {
  font-size: 2.2rem;
}

.color-picker {
  display: flex;
  width: 120px;
  justify-content: space-between;
  align-items: center;
  margin-left: 1rem;
  border: 1px solid #2980b9;
  border: 1px solid var(--color-primary);
  padding: 5px;
  border-radius: 5px;
}

.btn-theme {
  height: 20px;
  width: 20px;
  outline: none;
  border-radius: 50%;
  border: 1px solid #2980b9;
  border: 1px solid var(--color-primary);
  cursor: pointer;
  -webkit-transition: -webkit-transform .3s ease-out;
  transition: -webkit-transform .3s ease-out;
  transition: transform .3s ease-out;
  transition: transform .3s ease-out, -webkit-transform .3s ease-out;
}

.btn-theme:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

