/*============-============-============-============
                      PAGE LOADER
==============-============-============-==========*/

.loader-wrapper {
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.loader {
  width: 150px;
  height: 150px;
  border: 16px solid #f3f3f3;
  border-radius: 50%;
  border-top: 16px solid #3498db;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

/*============-============-============-============
                     MEDIA QUERIES
==============-============-============-==========*/

@media (min-width: 741px ) {
  .photo-list li:first-of-type {
    grid-column: 1 / 3;
  }
}

@media (min-width: 1101px ) {
  .photo-list {
    grid-template-rows: repeat(4, calc(100vh / 4 - 20px));
    grid-auto-rows: unset;
  }
  .photo-list li:first-of-type {
    grid-column: 2 / 3;
    grid-row: 2 / 4;
  }
  .photo-header {
    border-style: solid;
    border-width: 5px;
    border-radius: 5px;
  }
}

@media (max-width: 1100px ) {
  .photo-list .photo-header {
    border: none;
  }
}

@media (min-width: 1101px) and (max-width: 1460px) {
  .photo-list li:nth-of-type(2),
  .photo-list li:nth-of-type(4) {
    grid-row: 1 / 3;
  }
  .photo-list li:nth-of-type(5),
  .photo-list li:nth-of-type(7) {
    grid-row: 3 / 5;
  }
  .photo-list li:nth-of-type(n + 8) {
    display: none;
  }
}

@media (min-width: 1461px ) {
  .photo-list {
    grid-template-rows: repeat(3, calc(100vh / 3 - 20px));
  }
  .photo-list li:first-of-type {
    grid-column: 2 / 4;
    grid-row: 2 / 3;
  }
  .photo-header span {
    font-size: 2.4rem;
  }
}
